:root, ::before, ::after{
  --white: #fff;
  --white_d1: #f2f2f2;

  /*--grey_l2: #d3d3d3;*/
  --grey_l1: #B3B3B3;
  /*--grey: #808080;*/
  --grey_d1: #4D4D4D;
  --grey_d2: #333;

  --black_l2: #1A1A1A;
  --black0_l2: #1A1A1A00;
  --black5_l2: #1a1a1a80;
  --black_l1: #111;
  --black0_l1: #11111100;
  --black: #000;

  --gold_l1: #D1B585;
  --gold5_l1: #D1B58580;
  --gold: #A38D67;
  --gold0: #A38D6700;
  --gold5: #A38D6780;
  --gold_d1: #756549;
  
}

*, *::before, *::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

p, a, h1, h2, h3, h4, h5, h6, input, label, li, span, dl, dd, dt{
  font-family: 'SF Pro Display', sans-serif;
}

a{
  text-decoration: none;
  color: var(--white_d1);
}

html{ font-size: 8px; }

html, body{
  min-width: 100%;
  min-height: 100vh;
}

body{
  min-height: 100vh;
  background-color: var(--black_l2);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

main{
  overflow: hidden;
}