/*========= MENU =========*/
.menu{
    width: 100%;
    height: 7rem;
    padding: 0 4.3rem;
    background-color: var(--black_l2);
    display: grid;
    grid-template-columns: auto 1fr auto auto;
    align-content: center;
    align-items: center;
}
.menu__logo{
    height: 1.7rem;
}
.menu__logo img{
    height: 100%;
    cursor: pointer;
}
ul.menu__options{
    padding: 0 4rem;
    display: flex;
    gap: 4rem;
    font-size: 2rem;
    color: var(--grey_l1);
    list-style: none;
}
li.menu__option{
    cursor: pointer;
}
.menu__search_container{

}
.menu__search{

}
.menu__user{
    width: 4rem;
    height: 4rem;
    border-radius: 100%;
    background: var(--gold);
    overflow: hidden;
}
.menu__user_img{
    width: 100%;
    height: 100%;    
}




/*========= HEADER =========*/

.header{
    height: 84.8rem;
    background-color: var(--black_l1);
    overflow: hidden;
    position: relative;
}
.header::before{
    content: "";
    width: 100%;
    height: 17.6rem;
    background: linear-gradient(0deg, var(--black_l2) 0%, var(--black0_l2) 100%);
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 5;
}
.movie_info{
    width: 54rem;
    /* height: 100%; */
    margin-left: 17.6rem;
    margin-bottom: 30rem;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 10;
}
.movie_info__category{
    margin-bottom: 1rem;
    font-size: 1.8rem;
    letter-spacing: .1rem;
    text-transform: uppercase;
    color: var(--grey_l1);
}
.movie_info__logo_container{
    width: 48.8rem;
}
.movie_info__logo{
    width: 100%;
    margin-bottom: 1rem;
}
.movie_info__description{
    margin-bottom: 2rem;
    font-size: 2rem;
    color: var(--grey_l1);
    line-height: 2.6rem;
}
.movie_info__buttons{

}
.movie_info__button{
    margin-right: 1.4rem;
    padding: 1.6rem 5.2rem;
    border: .2rem solid var(--white);
    font-size: 1.8rem; 
    cursor: pointer;
}
.movie_info__button.button--play{
    background-color: var(--white);
    color: var(--black_l2);
}
.movie_info__button.button--info{    
    background-color: transparent;
    color: var(--white);
}
.movie_preview{
    height: 100%;
    aspect-ratio: 16/9;
    background-color: var(--black);
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(4.3rem);
}
.movie_preview::before{
    content: "";
    width: 6.7rem;
    height: 100%;
    background: var(--black_l1);
    position: absolute;
    top: 0;
    left: 0;
}
.movie_preview::after{
    content: "";
    width: 59.8rem;
    height: 100%;
    background: linear-gradient(90deg, var(--black_l1) 0%, var(--black0_l1) 100%);
    position: absolute;
    top: 0;
    left: 6.7rem;
}
.movie_preview img{
    width: 100%;
    height: auto;
}



/*========= FOOTER =========*/

.footer{
    width: 100%;
    height: 24rem;
    padding-top: 5.8rem;
    background-color: var(--black_l1);
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.6rem;
    color: var(--white_d1);
    /* position: absolute;
    bottom: 0; */
}
.footer__logo{
    width: 18rem;
    height: auto;
    margin-bottom: 3rem;
}
.footer__logo img{
    width: 100%;
    height: auto;
}
ul.footer__links{
    margin-bottom: 3rem;
    display: flex;
    gap: 3rem;
    list-style: none;
}
li.footer__link{
    cursor: pointer;
}
.foooter__text{
    opacity: .6;
}