.menu__search_container{
    margin-right: 4rem;
    padding: 0.2rem;
    border-top: .1rem solid var(--black_l2);
    border-bottom: .1rem solid var(--black_l2);
    display: flex;
    align-items: center;
}
.menu__search_container label{
    display: flex;
    justify-content: center;
    align-items: center;
}
.menu__search_icon{
    width: 2rem;
    height: 2rem;
    display: inline-block;
    color: var(--white_d1);
    cursor: pointer;
}
.menu__search_icon svg{
    fill: var(--white_d1)
}
.menu__search{
    width: 0rem;
    padding: 0.2rem 0;                
    border: none;
    background: transparent;
    font-size: 2rem;
    color: var(--white_d1);
    outline: none;
    transition: .2s;
}
.menu__search::-webkit-search-cancel-button{
    display: none;
}
.menu__search:focus{
    width: 20rem;
    padding: 0.4rem;
}
.menu__search___opened {
    border-bottom: .1rem solid var(--white);
}

.menu__search___opened .menu__search_icon{
    margin-right: 1rem;
}