/*--------- Card ---------*/

li.card{
    background-color: var(--grey_d2);
    box-shadow: 0 .6rem .6rem 0 rgba(0, 0, 0, .5);
    overflow: hidden;
    transition: .2s;
    cursor: pointer;
}
li.card:hover{
    box-shadow: 0 .6rem 1.2rem 0 rgba(0, 0, 0, .9);
    z-index: 2;
    transform: scale(1.02);    
}
.card__img_container{
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;
}
.card__img_container::before{
    content: "";
    width: 100%;
    height: 100%;
    border: .4rem solid var(--gold);
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    transition: .2s;
    opacity: 0;
}
.list_content--special_row .card__img_container::before{
    border: .4rem solid var(--white);
}
li.card:hover .card__img_container::before{
    border: .4rem solid var(--gold);
    opacity: 1;
}
.list_content--special_row li.card:hover .card__img_container::before{
    border: .4rem solid var(--white);
    opacity: 1;
}
.card__img{
    width: 100%;
    width: 100%;
}
