/*========= LISTS CONTENT =========*/

.lists_content{    
    transform: translateY(-17.6rem) translateX(17.6rem);
    /* overflow: hidden; */
    position: relative;
    z-index: 20;
}
.list_content{
    margin-bottom: calc(10rem - 2.8rem);
    transform: translateX(-17.6rem);
}
.list_content--double_row{

}
.list_content--special_list{
    padding-top: 5.2rem;
    padding-bottom: 8rem;
    background-color: var(--gold);
    background: linear-gradient(90deg, var(--gold_d1) 0%, var(--gold_l1) 100%);
}
.list_content__section{
    margin-left: 17.6rem;
    margin-bottom: 1.6rem;
    font-size: 2.8rem;
    color: var(--grey_l1);
}
.list_content--special_list .list_content__section{
    color: var(--white);
}
.list_content__cards_container{
    position: relative;
}
.lists_content__arrow{
    width: 15.6rem;
    height: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    z-index: 2;
    cursor: pointer;
}
.lists_content__arrow img{
    width: 6.4rem;
    height: 6.4rem;
}
.lists_content__arrow:hover img{
    opacity: .5;
}
.arrow___prev{
    display: none;
    left: 0;
    background: linear-gradient(90deg, var(--black_l2) 0%, var(--black5_l2) 100%);
}
.arrow___next{
    right: 0;
    background: linear-gradient(-90deg, var(--black_l2) 0%, var(--black5_l2) 100%);
}
.list_content--special_list .arrow___prev{
    background: linear-gradient(90deg, var(--gold_l1) 0%, var(--gold5_l1) 100%);
}
.list_content--special_list .arrow___next{
    background: linear-gradient(-90deg, var(--gold_l1) 0%, var(--gold5_l1) 100%);
}
ul.list_content__cards{
    display: grid;
    /*grid-template-columns: repeat(5, minmax(auto, 37.7rem));*/
    /*grid-template-columns: repeat(5, 37.7rem);*/
    grid-template-columns: repeat(20, 34rem); /* 30 --> 80%*/
    gap: 2rem;
    transform: translateX(17.6rem);
    list-style: none;
}
.list_content--double_row ul.list_content__cards{
    grid-template-rows: auto auto;
}




.lists_content{
    width: calc(100% - 35.2rem);
    min-height: 100vh;
    margin: 0 auto;
    margin-top: 12rem;
    margin-bottom: 16rem;
    transform: none;
    position: initial;
}
.lists_content::before{
    content: "";
    width: 100%;
    height: 45rem;
    background-color: var(--black_l1);
    position: absolute;
    top: 7rem;
    left: 0;
    z-index: -2;
}
.lists_content::after{
    content: "";
    width: 100%;
    height: 17.6rem;
    background: linear-gradient(0deg, var(--black_l2) 0%, var(--black0_l2) 100%);
    position: absolute;
    top: 34.4rem;
    left: 0;
    z-index: -1;
}
.list_content{
    transform: none;
}
.list_content__section{
    margin-left: 0;
}
.lists_content__arrow{
    display: none;
}
ul.list_content__cards{
    grid-template-columns: repeat(auto-fill, minmax(32rem, auto));
    transform: none;
}