/*========= HEADER =========*/

.header{
    height: 84.8rem;
    min-height: 100vh;
    background-color: var(--black_l1);
    overflow: hidden;
    position: relative;
}
.header::before{
    content: "";
    width: 100%;
    height: 17.6rem;
    background: linear-gradient(0deg, var(--black_l2) 0%, var(--black0_l2) 100%);
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 5;
}
.movie_info{
    width: 54rem;
    /* height: 100%; */
    margin-left: 17.6rem;
    margin-bottom: 30rem;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 10;
}
.movie_info__category{
    margin-bottom: 1rem;
    font-size: 1.8rem;
    letter-spacing: .1rem;
    text-transform: uppercase;
    color: var(--grey_l1);
}
.movie_info__logo_container{
    width: 48.8rem;
}
.movie_info__logo{
    width: 100%;
    margin-bottom: 1rem;
}
.movie_info__description{
    margin-bottom: 2rem;
    font-size: 2rem;
    color: var(--grey_l1);
    line-height: 2.6rem;
}
.movie_info__buttons{

}
.movie_info__button{
    margin-right: 1.4rem;
    padding: 1.6rem 5.2rem;
    border: .2rem solid var(--white);
    font-size: 1.8rem; 
    cursor: pointer;
}
.movie_info__button.button___play{
    background-color: var(--white);
    color: var(--black_l2);
}
.movie_info__button.button___info{    
    background-color: transparent;
    color: var(--white);
}
.movie_preview{
    height: 100%;
    aspect-ratio: 16/9;
    background-color: var(--black);
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(4.3rem);
}
.movie_preview::before{
    content: "";
    width: 6.7rem;
    height: 100%;
    background: var(--black_l1);
    position: absolute;
    top: 0;
    left: 0;
}
.movie_preview::after{
    content: "";
    width: 59.8rem;
    height: 100%;
    background: linear-gradient(90deg, var(--black_l1) 0%, var(--black0_l1) 100%);
    position: absolute;
    top: 0;
    left: 6.7rem;
}
.movie_preview img{
    width: 100%;
    height: auto;
}




/*========= LISTS CONTENT =========*/

.lists_content{    
    transform: translateY(-17.6rem) translateX(17.6rem);
    /* overflow: hidden; */
    position: relative;
    z-index: 20;
}
.list_content{
    margin-bottom: calc(10rem - 2.8rem);
    transform: translateX(-17.6rem);
}
.list_content--double_row{

}
.list_content--special_list{
    padding-top: 5.2rem;
    padding-bottom: 8rem;
    background-color: var(--gold);
    background: linear-gradient(90deg, var(--gold_d1) 0%, var(--gold_l1) 100%);
}
.list_content__section{
    margin-left: 17.6rem;
    margin-bottom: 1.6rem;
    font-size: 2.8rem;
    color: var(--grey_l1);
}
.list_content--special_list .list_content__section{
    color: var(--white);
}
.list_content__cards_container{
    position: relative;
}
.lists_content__arrow{
    width: 15.6rem;
    height: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    z-index: 2;
    cursor: pointer;
}
.lists_content__arrow svg{
    /* width: 6.4rem;
    height: 6.4rem; */
    font-size: 4rem;
    fill: var(--white);
}
.lists_content__arrow:hover svg{
    opacity: .5;
}
.arrow___prev{
    display: none;
    left: 0;
    background: linear-gradient(90deg, var(--black_l2) 0%, var(--black5_l2) 100%);
}
.arrow___next{
    right: 0;
    background: linear-gradient(-90deg, var(--black_l2) 0%, var(--black5_l2) 100%);
}
.list_content--special_list .arrow___prev{
    background: linear-gradient(90deg, var(--gold_l1) 0%, var(--gold5_l1) 100%);
}
.list_content--special_list .arrow___next{
    background: linear-gradient(-90deg, var(--gold_l1) 0%, var(--gold5_l1) 100%);
}
ul.list_content__cards{
    display: grid;
    /*grid-template-columns: repeat(5, minmax(auto, 37.7rem));*/
    /*grid-template-columns: repeat(5, 37.7rem);*/
    grid-template-columns: repeat(20, 34rem); /* 30 --> 80%*/
    gap: 2rem;
    transform: translateX(17.6rem);
    list-style: none;
}
.list_content--double_row ul.list_content__cards{
    grid-template-rows: auto auto;
}




section{
    transform: translateY(-3.6rem);
}
.header::before{
    height: 63.4rem;
    bottom: 7.3rem;
}
.header::after{
    content: "";
    width: 100%;
    height: 7.3rem;
    background-color: var(--black_l2);
    position: absolute;
    bottom: 0;
    left: 0;
}
.movie_info__logo{
    margin-bottom: 3rem;
}
.movie_info{
    margin-left: 27.6rem;
    margin-bottom: 16.6rem;
}
.movie_preview{
    width: calc(100% - 35.2rem);
    margin: 0 auto;
    position: initial;
    transform: none;
}
.movie_preview::before, .movie_preview::after{
    display: none;
}

.card_info{
    margin: 0 17.6rem;
    margin-top: -3.6rem;
    margin-bottom: 8.6rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 13.4rem;
    font-size: 2rem;
    color: var(--grey_l1);
}
.card_info__column:nth-child(1){
    padding-left: 10rem;
}
.card_info__column:nth-child(2){
    padding-right: 10rem;
}
.card_info__year_duration{
    margin-bottom: 1.5rem;
    color: var(--white_d1);
}
.card_info__description{

}
.more_info__content{
    border-collapse: separate;
    border-spacing: 0px 1.5rem;
}
.more_info__content dl{
    display: table-row;
}
.more_info__content dt{
    padding-right: 3.4rem;
    vertical-align: top;
    white-space: nowrap;
    color: var(--white_d1);
}
.more_info__content dt, .more_info__content dd{
    display: table-cell;
    float: none;
}

.lists_content{
    transform: translateX(17.6rem);
}
ul.list_content__cards{
    /* display: inline-flex; */
}
li.card{
    width: 37.7rem;
}